@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital@0;1&display=swap');

body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: 'Roboto Mono', monospace;
}


.jeje{
  background-color: blueviolet;
}

li{
  padding: 1rem;
  border: 1px solid #2D4263;
  margin-bottom: 5px;
}
ul{
  list-style:none;
  display:inline-block;
  padding-inline-start: 0px;
  width: 300px;
}
.note {
  color: grey;
  padding-top: 5px;
  font-size: 15px;
}

h1 {
  color: green;
  font-style: italic;
}
input{
  border: 0;
  border-bottom: 1px solid #C84B31;
  outline: 0;
  background-color:  #191919;
  color: #ECDBBA;
  font-size: large;
}
.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.succes{
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

body{
  background-color: #191919;
  color: #ECDBBA;
}

button {
  color: #090909;
  padding: 0.2em 1em;
  font-size: 18px;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  transition: all .3s;
  margin-top: 10px;
 }
 
 button:hover {
  border: 1px solid white;
 }
 
 